.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  color: whitesmoke;
  font-family: 'Permanent Marker', cursive;
  font-size: 2rem; 
}

img {
  width: 25%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
}

.movieApp {
  font-size: 6rem;
  background-size: cover;
  background-repeat: no-repeat;
}

h1 {
  margin: 0 auto;
  font-family: 'Lobster', cursive;
  font-size: 11rem;
  text-shadow: 6px 6px rgb(138, 10, 10);
}

button {
  background-color: rgb(138, 10, 10);
  border: 4px solid rgb(138, 10, 10);
  border-radius: 35px;
  padding: 20px;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bolder;
  color: whitesmoke;
  text-align: center;
  margin-top: 40px;
}

button:hover,
button:focus {
  background-color: white;
  color: rgb(4, 66, 32);
  border: 4px solid rgb(4, 66, 32);
  font-weight: bolder;
  cursor: pointer;
}

.landingPage {
  background-image: url(./header-image2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.landingPage .wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 95vh;
}

.movies {
  padding: 100px 0;
  font-family: 'Lobster', cursive;
  font-size: 3rem;
  background-color: #000;
}

.movieContainer {
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  border-radius: 10px;
  width: 80%;
}

.movies h2 {
  margin-bottom: 40px;
  color: rgb(138, 10, 10);
}

.movies p {
  font-family: 'Permanent Marker', cursive;
  width: 60%;
  margin-left: 50px;
  color: #000;
}

.movieFlexContainer {
  display: flex;
  align-items: center;
}

.movieFlexContainer img {
  min-width: 200px;
  height: auto;
}

.footer {
  background-color: rgb(4, 66, 32);
  width: 100%;
  height: 5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
}

footer a {
  color: white;
  font-size: 1.4rem;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
}

footer a:hover,
footer a:focus {
  border-bottom: 1px solid #fff;
}

@media (max-width:1016px) {
  h1 {
    font-size: 9rem;
  }
  .movieFlexContainer {
    flex-direction: column;
  }
  .movieFlexContainer img {
    min-width: 300px;
  }
  .movieFlexContainer p {
    margin-left: 0;
    margin-top: 15px;
    text-align: center;
    font-size: 2rem;
    width: 100%;
  }
}

@media (max-width:898px) {
  .movies h2 {
    margin-bottom: 15px;
  }
  h1 {
    font-size: 7rem;
  }
}

@media (max-width:614px) {
  h1 {
    font-size: 5rem;
  }

}

@media (max-width:444px) {
  h1 {
    font-size: 3.5rem;
  }
  .landingPage {
    height: 90vh;
  }
  h2 {
    font-size: 3rem;
  }
  button {
    margin-top: 20px;
    padding: 10px;
    font-weight: 400;
  }
  .movieFlexContainer img {
    min-width: 200px;
  }
  .footer {
    height: 10vh;
  }
}
